.headerforsite {
  background-image: url('../img/backbootle.png');
  background-repeat: no-repeat;
  background-size: cover;
  height: 80vh;
  position: relative;
  @media only screen and (max-width: 1200px) and (min-width: 1200px) {
    height: 50vh; }
  @media only screen and (max-width: 768px) {
    height: 100vh; }
  @media only screen and (max-width: 375px) {
    height: 100%; } }
.headerlen__wrap {
  display: flex;
  padding-top: 30px;
  justify-content: space-between;
  padding-bottom: 45px;
  @media only screen and (max-width: 375px) {
    .logolen {
      padding-right: 30px; }
    .contactlen {
      display: flex;
      flex-direction: column; }
    .contactbotle {
      flex-direction: column;
      a {
        font-size: 15px; } }
    .deliverybotle {
      p {
        font-size: 15px; } } } }
.contactbotle {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15px;
  a {
    font-size: 18px;
    font-family: "OpenSans";
    font-weight: lighter;
    color: #dde7f2;
    text-decoration: none; } }
.deliverybotle {
  p {
    font-size: 18px;
    font-family: "OpenSans";
    font-weight: lighter;
    color: #dde7f2; } }
.botlesale__wrap {
  padding-bottom: 60px;
  .botle-head {
    h1 {
      font-size: 30px;
      font-family: "OpenSans";
      font-weight: bold;
      text-transform: uppercase;
      padding-bottom: 25px;
      color: #dde7f2; }
    p {
      font-size: 18px;
      font-family: "OpenSans";
      font-weight: normal;
      text-transform: uppercase;
      color: #dde7f2; }
    @media only screen and (max-width: 375px) {
      h1 {
        font-size: 26px; }
      p {
        font-size: 17px; } } } }
.botlefunction {
  padding-left: 30px;
  .functionall {
    display: flex;
    align-items: center;
    padding-bottom: 50px;
    p {
      color: #dde7f2;
      font-size: 16px;
      font-family: "OpenSans";
      font-weight: bold;
      width: 33%;
      line-height: 1.5;
      margin-right: 40px; }
    .linebotle {
      height: 2px;
      width: 31%;
      background-color: #fff; } }
  .botleimg {
    position: absolute;
    right: 17%;
    top: 15%;
    @media only screen and (max-width: 1200px) and (min-width: 1024px) {
      transform: translateX(72%);
      width: 30%;
      right: 25%;
      display: block;
      img {
        width: 100%; } }
    @media only screen and (max-width: 768px) {
      width: 35%;
      right: 0%;
      top: 25%;
      img {
        width: 100%; } } } }
.botletimer {
  width: 850px;
  height: 190px;
  background-color: #ecf9fd;
  border-radius: 25px;
  box-shadow: 0px 0px 48px -8px rgba(0,0,0,1);
  @media only screen and (max-width: 768px) {
    width: 735px; }
  @media only screen and (max-width: 375px) {
    display: flex;
    width: 100%;
    flex-direction: column;
    height: 100%; }
  .botletimer-txt {
    padding: 20px;
    h4 {
      font-size: 18px;
      font-family: "OpenSans";
      font-weight: bold;
      text-transform: uppercase;
      padding-bottom: 10px; }
    p {
      font-size: 16px;
      font-family: "OpenSans";
      font-weight: normal; }
    @media only screen and (max-width: 375px) {
      width: 100%; } }
  .botletimer-items {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @media only screen and (max-width: 375px) {
      display: flex;
      flex-direction: column; }
    .botletimer-items-all.one {
      padding-left: 20px;
      width: 30%;
      p {
        font-size: 48px;
        font-weight: bold;
        font-family: "OpenSans";
        color: #1cc4ca;
        display: inline-block; }
      span {
        font-size: 24px;
        font-weight: bold;
        font-family: "OpenSans";
        text-decoration:  line-through;
        display: inline-block; }
      @media only screen and (max-width: 768px) {
        p {
          font-size: 30px; }
        span {
          font-size: 20px; } }
      @media only screen and (max-width: 375px) {
        padding-left: 0;
        width: 60%;
        padding-bottom: 15px; } }
    .botletimer-items-all.two {
      width: 20%;
      p {
        font-size: 18px;
        font-weight: normal;
        font-family: "OpenSans"; }
      @media only screen and (max-width: 768px) {
        p {
          font-size: 15px; } }
      @media only screen and (max-width: 375px) {
        width: 100%;
        padding-bottom: 15px;
        p {
          text-align: center; } } }
    .botletimer-items-all-flex {
      display: flex;
      @media only screen and (max-width: 375px) {
        padding-bottom: 15px; } }
    .botletimer-items-all.three {
      margin-right: 15px;
      background-color: #fff;
      width: 90px;
      height: 60px;
      border-radius: 15px;
      p {
        font-size: 36px;
        font-weight: bold;
        font-family: "OpenSans";
        color: #1cc4ca;
        text-align: center; }
      span {
        font-size: 14px;
        font-weight: bold;
        font-family: "OpenSans";
        color: #c0bebe;
        display: block;
        text-align: center; } }
    button {
      margin-left: 10px;
      margin-right: 20px;
      width: 240px;
      height: 55px;
      background-color: #1cc4ca;
      border: none;
      border-radius: 25px;
      font-size: 24px;
      font-weight: bold;
      font-family: "OpenSans";
      color: #fff;
      @media only screen and (max-width: 375px) {
        margin-left: 0;
        margin-right: 0;
        display: block;
        margin: auto;
        margin-bottom: 20px; } } } }
